@mixin buttonStyle{
  width: 100px;
  align-self: center;
  border-radius: 10px;
  font-family: "Quicksand";
  font-size: 1.2rem;
  font-weight: 600;
  padding: 5px;
  background-color: #129494;
  border: 5px solid #384f4f;
  color: white;
  &:hover{
    background-color: #352745c6;
    cursor: pointer;
};
}

.form{
  padding-bottom: 70px;
  &__container{
    height:450px;
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    font-size: 1.4em;
    padding: 52px 24px;
    background-color: #152630;
    border: 5px solid #384f4f ;
    border-radius: 25px;
    font-weight: 600;
    color: white;
    @media all and (max-width: 600px){
      box-sizing: border-box;
      width: 95%;
      margin: 0 auto;
      height: fit-content;
    }
    @media all and (max-width: 376px){
      font-size: 1rem;
      padding: 16px;
      border-radius: 0;
      width: 100%;
    }
    &__input{
      display: flex;
      justify-content: space-between;
      width: 100%;
      font-family: "Quicksand";
      @media all and (max-width: 600px){
        flex-direction: column;
      }
      label{
        padding-bottom:5px;
      }
      input[type=text], input[type=email], textarea{
        font-size: 1.1rem;
      }
      input[type=text], input[type=email]{
        width: 80%;
        height: 30px;
        margin: 0;
        @media all and (max-width: 600px){
          width: 100%;
        }
      }
      textarea{
        width: 80%;
        height:80px;
        @media all and (max-width: 600px){
          width: 100%;
          height: 150px;
        }
      }
    }
  }
  
  input[type=submit]{
    @include buttonStyle();
  }
  
}